.fleets-section {
  background-color: #1F283C;
  color: #fff;
}

.fleets-wrap {
  display: flex;
  justify-content: space-between;
}

.fleet-item {
  width: 32%;
  border-radius: 4px;
  overflow: hidden;
}

.fleet-item:hover img {
  transform: scale(1.1);
}

.fleet-item .fleet-thumb {
  display: block;
  width: 100%;
  height: 320px;
  overflow: hidden;
  margin-bottom: 20px;
}

.fleet-item .fleet-thumb img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: .3s all ease;
}

.fleet-item h4 a {
  color: #fff;
  font-size: 22px;
  font-weight: 500;
  line-height: 30px;
  margin-bottom: 10px;
}

.fleet-item p {
  font-size: 15px;
  line-height: 22px;
  color: #A6AEBE;
}

#fleets-wrap_mobile {
  display: none;
}

@media screen and (max-width: 990px) {
  .fleets-wrap {
    display: none;
  }
  #fleets-wrap_mobile {
    display: block;
  }
  #fleets-wrap_mobile .owl-stage {
    padding-left: 60px !important;
  }
  .fleet-item {
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  .fleets-section .title-wrap .title-text_wrap h4 {
    margin-bottom: 15px;
  }
  #fleets-wrap_mobile .owl-stage {
    padding-left: 20px !important;
  }
  #fleets-wrap_mobile .owl-dots {
    display: none;
  }
  .fleet-item .fleet-thumb {
    height: 210px;
  }
  .fleet-item h4 {
    margin-bottom: 10px;
  }
  .fleet-item h4 a {
    font-size: 21px;
  }
  .fleet-item p {
    font-size: 14px;
  }
}

